.footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 30px;
    margin-top: 20px;
}
.footer-logo{
    display: flex;
    gap: 15px;
    align-items: center;
    justify-content: center;
}
.footer-logo img{
    width: 40px;
   height: 40px;
   border-radius: 50%;
}
.footer-logo p{
    font-size: 20px;
    font-weight: 600;
}
.footer ul{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px;
    font-size: 14px;
    font-weight: 500;
}
.footer li{
    cursor: pointer;
}
.footer li:hover{
    color: orangered;
}
.social-media-icons{
    display: flex;
    gap: 20px;
}
.social-links img{
    width: 60px;
}
.social-links-whatsapp{
    width: 48px;
    margin-top: 2px;
}
.social-links-facebook{
    width: 68px;
    margin-top: 2px;
}
.footer-copyright hr{
    width: 90%;
}
.footer-copyright{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
gap: 30px;
width: 100%;
margin-bottom: 30px;
color: black;
font-size: 14px;
}
@media (max-width: 768px){
    .footer{
        gap: 15px;
        margin-top: 10px;
    }
    .footer-logo{
        gap: 10px;
    }
    .footer-logo img{
        width: 20px;
       height: 20px;
       border-radius: 50%;
    }
    .footer-logo p{
        font-size: 15px;
    }
    .footer ul{
        gap: 15px;
        font-size: 10px;
    }
    .social-media-icons{
        gap: 15px;
    }
    .social-links img{
        width: 40px;
    }
    .social-links-whatsapp{
        width: 30px;
    }
    .social-links-facebook{
        width: 48px;
    }
    .footer-copyright hr{
        width: 90%;
    }
    .footer-copyright{
    gap: 15px;
    width: 100%;
    margin-bottom: 15px;
    color: black;
    font-size: 10px;
    }
}
@media (max-width: 1024px){
    .footer{
        gap: 20px;
        margin-top: 18px;
    }
    .footer-logo{
        gap: 12px;
    }
    .footer-logo img{
        width: 30px;
       height: 30px;
       border-radius: 50%;
    }
    .footer-logo p{
        font-size: 15px;
    }
    .footer ul{
        gap: 20px;
        font-size: 12px;
    }
    .social-media-icons{
        gap: 18px;
    }
    .social-links img{
        width: 50px;
    }
    .social-links-whatsapp{
        width: 40px;
        margin-top: 2px;
    }
    .social-links-facebook{
        width: 60px;
        margin-top: 2px;
    }
    .footer-copyright hr{
        width: 90%;
    }
    .footer-copyright{
    gap: 20px;
    width: 100%;
    margin-bottom: 20px;
    color: black;
    font-size: 12px;
    }
}