.productdisplay-img-list{
    width: 80px;
}
.productdisplay{
    margin-top: 50px;
}
.productdisplay-left{
    display: flex;
    gap: 20px;
    margin-left: 90px;
}
.productdisplay-main-img{
    width: 335px;
}
.productdisplay-right{
    display: flex;
    flex-direction: column;
    margin-left: 90px;
}
.productdisplay-right h1{
    font-size: 40px;
     
}
.productdisplay-right p{
    font-size: 20px;
   font-weight: 600;
   margin-bottom: 30px;
}
.productdisplay-right-size1{
    margin-left: 90px;
}
.productdisplay-right-size{
    gap: 20px;
   margin: 10px 0px;
   display: flex;
}
.productdisplay-right-size1 h1{
  font-size: 16px;
  font-weight: 500;
}
.productdisplay-right-size div{
    padding: 4px 8px;
    background: darkgray;
    color: black;
    font-size: 13px;
    cursor: pointer;
    border-radius: 3px;
    border: 1px solid #ebebeb;
}
.button-button{
    width: 160px;
    height: 35px;
    color: white;
    background-color: orangered;
    font-weight: 600;
    border: none;
    border-radius: 2px;
    cursor: pointer;
    font-size: 14px;
   margin-left: 90px;
   margin-top: 30px;
}
.button-button{
    background: orange;
    transform: scale(1.05);
    transition: 0.6s;
    color: black;
}
.productdisplay-right-category{
    margin-top: 15px;
    margin-left: 90px;
}
.productdisplay-right-category span{
    font-weight: 600;
}
@media (max-width: 768px){
    .productdisplay-img-list{
        width: 40px;
    }
    .productdisplay{
        margin-top: 30px;
    }
    .productdisplay-left{
        display: block;
        margin-left: 60px;
    }
    .productdisplay-main-img{
        width: 168px;
        margin-bottom: 15px;
    }
    .productdisplay-right{
        display: flex;
        flex-direction: column;
        margin-left: 60px;
    }
    .productdisplay-right h1{
        font-size: 20px;
    }
    .productdisplay-right p{
        font-size: 15px;
       font-weight: 600;
       margin-bottom: 15px;
    }
    .productdisplay-right-size1{
        margin-left: 60px;
    }
    .productdisplay-right-size{
        gap: 15px;
       margin: 4px 0px;
    }
    .productdisplay-right-size1 h1{
      font-size: 6px;
    }
    .productdisplay-right-size div{
        padding: 3px 6px;
        font-size: 9px;
    }
    .button-button{
        width: 80px;
        height: 25px;
        font-size: 10px;
       margin-left: 60px;
       margin-top: 15px;
    }
    .productdisplay-right-category{
        margin-top: 10px;
        margin-left: 60px;
    }
}
@media (max-width: 1024px){
    .productdisplay-img-list{
        width: 60px;
    }
    .productdisplay{
        margin-top: 40px;
    }
    .productdisplay-left{
        gap: 18px;
        margin-left: 70px;
    }
    .productdisplay-main-img{
        width: 290px;
    }
    .productdisplay-right{
        margin-left: 70px;
    }
    .productdisplay-right h1{
        font-size: 30px; 
    }
    .productdisplay-right p{
        font-size: 18px;
       margin-bottom: 20px;
    }
    .productdisplay-right-size1{
        margin-left: 70px;
    }
    .productdisplay-right-size{
        gap: 18px;
       margin: 8px 0px;
       display: flex;
    }
    .productdisplay-right-size1 h1{
      font-size: 12px;
    }
    .productdisplay-right-size div{
        padding: 4px 6px;
        font-size: 11px;
    }
    .button-button{
        width: 100px;
        height: 28px;
        font-size: 12px;
       margin-left: 70px;
       margin-top: 20px;
    }
    .productdisplay-right-category{
        margin-top: 12px;
        margin-left: 70px;
    }
}