.item{
width: 230px;
}
.item p{
    margin: 8px 0;
    font-size: 15px;
    font-weight: 700;
}
.item img{
    width: 230px;
}
.item-price{
    font-size: 15px;
    color: black;
    font-weight: 600;
}
.item:hover{
  transform: scale(1.05);
  transition: 0.6s;  
}
.item button{
    width: 70px;
    height: 30px;
    background-color: orangered;
    color: white;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    border: none;
    border-radius: 2px;
}
.item button:hover{
    background: orange;
    transform: scale(1.05);
    transition: 0.6s;
    color: black;
}
@media (max-width: 768px){
    .item{
        width: 120px;
        }
        .item p{
            margin: 4px 0;
            font-size: 10px;
        }
        .item img{
            width: 120px;
        }
        .item-price{
            font-size: 10px;
        }
        .item button{
            width: 48px;
            height: 15px;
            font-size: 10px;
        }
}
@media (max-width: 1024px){
    .item{
        width: 190px;
        }
        .item p{
            margin: 6px 0;
            font-size: 12px;
        }
        .item img{
            width: 190px;
        }
        .item-price{
            font-size: 12px;
        }
        .item button{
            width: 55px;
            height: 20px;
            font-size: 12px;
        }
}