 .shopcategory-indexsort{
     display: flex;
     margin: 0px 100px;
     justify-content: space-between;
     align-items: center;
     margin-top: 30px;
 }
 .shopcategory-indexsort p span{
     font-weight: 600;
}
 .shopcategory-sort{
     display: flex;
     padding: 6px 18px;
     border-radius: 20px;
     border: 1px solid #888;
     gap: 15px;
 }
 .shopcategory-sort img{
     width: 15px;
     height: 15px;
     margin-top: 5px;
 }
 .shopcategory-product{
     display: grid;
     margin: 20px 170px;
     grid-template-columns: 1fr 1fr 1fr;
     row-gap: 80px;
 }
 @media (max-width: 768px){
    .shopcategory-indexsort{
        margin: 0px 60px;
        margin-top: 15px;
    }
    .shopcategory-sort{
        padding: 3px 9px;
        border-radius: 10px;
        gap: 8px;
    }
    .shopcategory-sort img{
        width: 8px;
        height: 8px;
        margin-top: 2px;
    }
    .shopcategory-product{
        margin: 10px 90px;
        grid-template-columns: 1fr 1fr;
        row-gap: 50px;
    }
 }
 @media (max-width: 1024px){
    .shopcategory-indexsort{
        margin: 0px 80px;
        margin-top: 20px;
    }
    .shopcategory-sort{
        padding: 6px 12px;
        border-radius: 16px;
        gap: 12px;
    }
    .shopcategory-sort img{
        width: 10px;
        height: 10px;
        margin-top: 3px;
    }
    .shopcategory-product{
        margin: 15px 120px;
        grid-template-columns: 1fr 1fr 1fr;
        row-gap: 65px;
    }
 }