.place-order{
    display: flex;
    align-items: flex-start;
  justify-content: space-between;
    margin-top: 100px;
    margin-bottom: 100px;
    margin-left: 20px;
    gap: 200px;
}
.place-order-left{
    width: 100%;
    max-width: max-content(10%, 500px);
}
.place-order-left .title{
    font-weight: 600;
    font-size: 30px;
    margin-bottom: 30px;
}
.place-order-left input{
    margin-bottom: 13px;
    width: 100%;
    padding: 10px;
    border: 1px solid #e5e5e5;
    border-radius: 2px;
    outline-color: orangered;
}
 .place-order-right{
     width: 100%;
     max-width: max-content(40%, 500px);
     margin-right: 0px;
 }
 .place-order-right-button{
     margin-top: 40px;
 }
 @media (max-width: 768px){
    .place-order{
        margin-top: 60px;
        margin-bottom: 60px;
        margin-left: 14px;
        gap: 100px;
    }
    .place-order-left .title{
        font-size: 15px;
        margin-bottom: 15px;
    }
    .place-order-left input{
        margin-bottom: 9px;
        padding: 6px;
    }
     .place-order-right-button{
         margin-top: 20px;
     }
 }
 @media (max-width: 1024px){
    .place-order{
        margin-top: 80px;
        margin-bottom: 80px;
        margin-left: 16px;
        gap: 170px;
    }
    .place-order-left .title{
        font-size: 20px;
        margin-bottom: 20px;
    }
    .place-order-left input{
        margin-bottom: 11px;
        padding: 8px;
    }
     .place-order-right-button{
         margin-top: 30px;
     }
 }