.newCollection{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    margin-bottom: 100px;
}
.newCollection h1{
    color: #171717;
    font-weight: 600;
    font-size: 30px;
    text-align: center;
}
.newCollection hr{
    width: 500px;
    height: 1.5px;
    background: #252525;
}
 .new-products-collection{
     display: grid;
     grid-template-columns: 1fr 1fr 1fr 1fr;
     margin-top: 50px;
     gap: 30px;
 }
 @media (max-width: 768px){
    .newCollection{
        gap: 10px;
        margin-bottom: 60px;
    }
    .newCollection h1{
        font-size: 15px;
    }
    .newCollection hr{
        width: 350px;
        height: 1px;
        background: #252525;
    }
     .new-products-collection{
         display: grid;
         grid-template-columns: 1fr 1fr;
         margin-top: 30px;
         gap: 15px;
     }
 }
 @media (max-width: 1024px){
    .newCollection{
        gap: 12px;
        margin-bottom: 70px;
    }
    .newCollection h1{
        font-size: 20px;
    }
    .newCollection hr{
        width: 400px;
        height: 1.2px;
        background: #252525;
    }
     .new-products-collection{
         display: grid;
         grid-template-columns: 1fr 1fr 1fr;
         margin-top: 35px;
         gap: 20px;
     }
}