.loginsignup{
    width: 100%;
    height: 80vh;
    background: #ffdacc;
    padding-top: 100px;
    padding-bottom: 100px;
}
.loginsignup-container{
    width: 580px;
    height: 400px;
    background: white;
    margin: auto;
    padding: 60px 90px;
}
.loginsignup-container h1{
    margin: 20px 0px;
    font-weight: 600;
    font-size: 25px;
}
.link{
    font-weight: 500;
    font-size: 12px;
    color: orangered;
    cursor: pointer;
}

.loginsignup-field{
    display: flex;
    gap: 0px;
    flex-direction: column;
    margin-top: 30px;
}
.loginsignup-field input{
    height: 40px;
    width: 100%;
    outline: none;
    padding-left: 20px;
    color: #5c5c5c;
    border: 1px solid #c9c9c9;
    font-size: 15px;
}
.loginsignup-container button{
    width: 100px;
    height: 35px;
    color: white;
    background-color: orangered;
    font-weight: 600;
    border: none;
    border-radius: 2px;
    cursor: pointer;
    font-size: 14px;
    margin-top: 30px;
}
.loginsignup-container button:hover{
    background: orange;
    transform: scale(1.05);
    transition: 0.6s;
    color: black;
}
.loginsignup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-weight: 500;
    font-size: 15px;
}
.message {
    margin: 10px 0;
    font-weight: bold;
    text-align: center;
    font-size: 1.5rem;
  }
  .message.success {
    color: green;
  }
  .message.error {
    color: red;
  }
  @media (max-width: 768px){
    .loginsignup{
        height: 45vh;
        padding-top: 60px;
        padding-bottom: 60px;
    }
    .loginsignup-container{
        width: 300px;
        height: 200px;
        padding: 35px 50px;
    }
    .loginsignup-container h1{
        margin: 15px 0px;
        font-size: 13px;
    }
    .link{
        font-size: 7px;
    }
    
    .loginsignup-field{
        margin-top: 15px;
    }
    .loginsignup-field input{
        height: 20px;
        padding-left: 15px;
        font-size: 10px;
    }
    .loginsignup-container button{
        width: 60px;
        height: 20px;
        font-size: 9px;
        margin-top: 15px;
    }
    .loginsignup-login{
        margin-top: 15px;
        font-size: 10px;
    }
    .message {
        margin: 6px 0;
        font-size: 0.8rem;
      }
  }
  @media (max-width: 1024px){
    .loginsignup{
        height: 65vh;
        padding-top: 80px;
        padding-bottom: 80px;
    }
    .loginsignup-container{
        width: 490px;
        height: 300px;
        padding: 48px 78px;
    }
    .loginsignup-container h1{
        margin: 17px 0px;
        font-size: 20px;
    }
    .link{
        font-size: 10px;
    }
    .loginsignup-field{
        margin-top: 20px;
    }
    .loginsignup-field input{
        height: 30px;
        padding-left: 17px;
        font-size: 12px;
    }
    .loginsignup-container button{
        width: 80px;
        height: 28px;
        font-size: 12px;
        margin-top: 20px;
    }
    .loginsignup-login{
        margin-top: 18px;
        font-size: 12px;
    }
    .message {
        margin: 8px 0;
        font-size: 1rem;
      }
  }