.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    height: 90vh;
    margin-bottom: 90px;
}
.popular h1{
    color: #171717;
    font-weight: 600;
    font-size: 30px;
    text-align: center;
}
.popular hr{
    width: 400px;
    height: 1.5px;
    background: #252525;
}
.popular-item{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}
@media (max-width: 768px){
    .popular{
        gap: 10px;
        height: 65vh;
        margin-bottom: 60px;
    }
    .popular h1{
        font-size: 15px;
    }
    .popular hr{
        width: 200px;
        height: 1px;
    }
    .popular-item{
        margin-top: 30px;
        gap: 15px;
    }
}
@media (max-width: 1024px){
    .popular{
        gap: 12px;
        height: 80vh;
        margin-bottom: 70px;
    }
    .popular h1{
        font-size: 20px;
    }
    .popular hr{
        width: 300px;
        height: 1.2px;
    }
    .popular-item{
        margin-top: 40px;
        gap: 20px;
    }
}