.breadcrum img{
    width: 12px;
    height: 12px;
    margin-top: 0.1%;
  }
  .breadcrum {
    color: #5e5e5e;
    font-size: 15px;
    display:flex;
    font-weight: 600;
    gap: 10px;
    align-items: center;
    margin-top: 20px;
  }
  @media (max-width: 768px){
    .breadcrum img{
      width: 8px;
      height: 8px;
    }
    .breadcrum {
      font-size: 10px;
      gap: 7px;
      margin-top: 15px;
    }
  }
  @media (max-width: 1024px){
    .breadcrum img{
      width: 10px;
      height: 10px;
    }
    .breadcrum {
      font-size: 12px;
      gap: 8px;
      margin-top: 18px;
    }
  }