nav{
    display: flex;
    background-color: white;
    color: black;
    align-items: center;
    justify-content: space-around;
    padding: 15px;
    box-shadow: 0 1px 3px -2px black;
}
.company-logo{
    display: inline-flex;
    gap: 15px;
    align-items: center;
    justify-content: center;
}
.company-logo img{
   width: 40px;
   height: 40px;
   border-radius: 50%;
}
.company-logo p{
    font-size: 20px;
    font-weight: 600;
}
nav ul{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
    font-size: 14px;
    font-weight: 500;
    color: black;
}
nav ul li{
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    color: black;
}
 
.cart-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px;
}
.btn{
    padding: 8px 22px;
    background-color: white;
    border: 1px solid black;
    border-radius: 18px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
}
.btn:hover{
    background-color: white;
    font-weight: 600;
    border: none;
}
.cart{
    width: 50px;
    margin-top: -5px;
}
.count{
    padding: 2px 6px;
    background-color: orangered;
    font-size: 12px;
    font-weight: 600;
    border: none;
    border-radius: 50%;
    margin-left: -42px;
    margin-top: -30px;
    color: white;
}
@media (max-width: 768px){
    nav{
        padding: 1px;
        gap: 9px;
        justify-content: center;
    } 
    .company-logo{
        gap: 10px;
        margin-left: -80px;
    }
    .company-logo img{
        width: 20px;
        height: 20px;
        border-radius: 50%;
     }
     .company-logo p{
        font-size: 15px;
    }
    nav ul{
        gap: 25px;
        font-size: 10px;
    }
    nav ul li{
        font-size: 10px;
    }
     
    .cart-btn{
        gap: 20px;
    }
    .btn{
        padding: 4px 12px;
        border-radius: 18px;
        font-size: 10px;
    }
    .cart{
        width: 30px;
        margin-top: 5px;
    }
    .count{
        padding: 2px 6px;
        background-color: orangered;
        font-size: 8px;
        border-radius: 50%;
        margin-left: -30px;
        margin-top: -22px;
    }
}

@media (max-width: 1024px){
    nav{
        padding: 10px;
        gap: 90px;
        justify-content: center;
    }
    .company-logo{
        gap: 12px;
        margin-left: -250px;
    }
    .company-logo img{
        width: 30px;
        height: 30px;
        border-radius: 50%;
     }
     .company-logo p{
        font-size: 18px;
    }
    nav ul{
        gap: 35px;
        font-size: 14px;
    }
    nav ul li{
        font-size: 14px;
    }
     
    .cart-btn{
        gap: 20px;
    }
    .btn{
        padding: 6px 16px;
        border-radius: 18px;
        font-size: 12px;
    }
    .cart{
        width: 40px;
        margin-top: 1px;
    }
    .count{
        padding: 2px 6px;
        background-color: orangered;
        font-size: 8px;
        border-radius: 50%;
        margin-left: -30px;
        margin-top: -22px;
    }
}