.hero-section{
height: 100vh;
background: linear-gradient(180deg, #FFCBA4, #e1ffea22 60%);
display: flex;
margin-bottom: 90px;
}
.hero-left{
flex: 1;
display: flex;
flex-direction: column;
justify-content: center;
gap: 20px;
padding-left: 110px;
line-height: 1.1;
}
.hero-left p{
    color: #171717;
    font-size: 20px;
    font-weight: 600;
}
.hero-left h1{
    font-size: 75px;
    font-weight: 700;
    color: 171717;
    margin: 0;
}
.new{
    display: flex;
    align-items: center;
    gap: 20px;
    margin-left: 450px;
    margin-top: -300px;

}
.new-img{
    width: 150px;
   
}
.hero-btn{
    height: 35px;
    background-color: orangered;
    color: white;
    font-weight: 600;
    font-size: 14px;
    border: none;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    margin-top: 30px;
    width: 160px;
}
.hero-btn:hover{
    background: orange;
    transform: scale(1.05);
    transition: 0.6s;
    color: black;
}
.hero-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
@media (max-width: 768px){
    .hero-section{
        margin-bottom: 50px;
        }
        .hero-left{
        gap: 15px;
        padding-left: 60px;
        line-height: 0.7;
        }
        .hero-left p{
            font-size: 15px;
        }
        .hero-left h1{
            font-size: 50px;
        }
        .new{
            gap: 15px;
            margin-left: 250px;
            margin-top: -150px;
        
        }
        .new-img{
            width: 70px; 
        }
        .hero-btn{
            height: 20px;
            font-size: 10px;
            gap: 10px;
            margin-top: 15px;
            width: 90px;
        }
}
@media (max-width: 1024px){
    .hero-section{
        height: 100vh;
        background: linear-gradient(180deg, #FFCBA4, #e1ffea22 60%);
        display: flex;
        margin-bottom: 70px;
        }
        .hero-left{
        gap: 15px;
        padding-left: 80px;
        line-height: 0.9;
        }
        .hero-left p{
            color: #171717;
            font-size: 15px;
            font-weight: 600;
        }
        .hero-left h1{
            font-size: 65px;
        }
        .new{
            gap: 15px;
            margin-left: 350px;
            margin-top: -190px;
        
        }
        .new-img{
            width: 100px;
           
        }
        .hero-btn{
            height: 30px;
            font-size: 12px;
            gap: 12px;
            margin-top: 22px;
            width: 100px;
        }
}